import React, { Component } from "react";
import {
  Route,
  NavLink,
  HashRouter
} from "react-router-dom";
import Home from "./Home";
import Stuff from "./Stuff";
import Contact from "./Contact";
import ThreeTest from "./ThreeTest";

class Letter extends React.Component {
    render() {
      var letterStyle = {
          padding: 10,
          margin: 10,
           backgroundColor: this.props.bgcolor,
          color: this.props.color,
          display: "inline-block",
          fontFamily: "monospace",
          fontSize: 32,
          textAlign: "center"
        };

      return(
        <div style={letterStyle}>
          {this.props.children}
        </div>
      );
    }
  }
 
class Main extends Component {
  render() {
    return (
        <HashRouter>
        <div>
          <h1>
      <Letter bgcolor="#58B3FF" color="#333">J</Letter>
      <Letter bgcolor="#FF605F" color="#fff">A</Letter>
      <Letter bgcolor="#58B3FF" color="#333">Y</Letter>
      <Letter bgcolor="#FF605F" color="#fff">D</Letter>
      <Letter bgcolor="#58B3FF" color="#333">E</Letter>
      <Letter bgcolor="#FF605F" color="#fff">M</Letter>
      <Letter bgcolor="#58B3FF" color="#333">A</Letter>
      <Letter bgcolor="#FF605F" color="#fff">R</Letter>
      <Letter bgcolor="#58B3FF" color="#333">I</Letter>
      <Letter bgcolor="#FF605F" color="#fff">E</Letter>
      <Letter bgcolor="#58B3FF" color="#333">B</Letter>
      <Letter bgcolor="#FF605F" color="#fff">E</Letter>
      <Letter bgcolor="#58B3FF" color="#333">L</Letter>
      <Letter bgcolor="#FF605F" color="#fff">L</Letter>
      <Letter bgcolor="#58B3FF" color="#333">.</Letter>
          </h1>
          <ul className="header">
            <li><NavLink to="/">Home</NavLink></li>
            <li><NavLink to="/stuff">Stuff</NavLink></li>
            <li><NavLink to="/contact">Contact</NavLink></li>
            <li><NavLink to="/threetest">ThreeTest</NavLink></li>
          </ul>
          <div className="content">
            <Route exact path="/" component={Home}/>
            <Route path="/stuff" component={Stuff}/>
            <Route path="/contact" component={Contact}/>
            <Route path="/threetest" component={ThreeTest}/>
          </div>
        </div>
      </HashRouter>
    );
  }
}
 
export default Main;